<template>
<div class="cardMain">
    <div class="left" :style="'background-image:url('+info.icon+')'"></div>
    <div class="right">
        <div class="name">{{info.name}}</div>
        <div class="content">{{info.content}}</div>
        <div class="link" v-if="JSON.stringify(info.link)!='{}'" @click="toPage(info.link.href)">
            <div class="img" :style="'background-image:url('+info.link.pic+')'"></div>
            <div class="content">{{info.link.title}}</div>
        </div>
        <div class="pic1" v-if="info.pic.length==1">
            <img :src="info.pic[0]" alt="图片不见了">
        </div>
        <div class="pic2" v-if="info.pic.length==2">
            <img :src="item" alt="图片不见了" v-for="(item, index) in info.pic" :key="index">
        </div>
        <div class="pic3" v-if="info.pic.length>=3">
            <img :src="item" alt="图片不见了" v-for="(item, index) in info.pic" :key="index">
        </div>
        <div class="stateArea">
            <div class="likeArea">
                <div :class="isLike?'like':'dislike'" @click="thumb"></div>
                <div>{{info.likes}}</div>
            </div>
            <div class="commentArea">
                <div></div>
                <div>评论</div>
            </div>
            <div class="deleteArea" v-if="isMine" @click="deleteItem">
                <div></div>
                <div>删除</div>
            </div>
        </div>
        <div class="timeArea">
            1小时前
        </div>
        <div class="commentArea" v-if="info.comments.length<=3">
            <div class="commentItem" v-for="(item, index) in info.comments" :key="index">
                <span v-if="!item.to"><span class="user">{{item.user}}:</span>{{item.content}}</span>
            </div>
        </div>
        <div class="commentArea" v-if="info.comments.length>3">
            <div v-if="!showAll">
                <div class="commentItem" v-for="(item, index) in info.comments.slice(0,3)" :key="index">
                    <span v-if="!item.to"><span class="user">{{item.user}}：</span>{{item.content}}</span>
                    <span v-else><span class="user">{{item.user}}</span>回复<span class="user">{{item.to}}</span>：{{item.content}}</span>
                </div>
            </div>
            <div v-else>
                <div class="commentItem" v-for="(item, index) in info.comments" :key="index">
                    <span v-if="!item.to"><span class="user">{{item.user}}：</span>{{item.content}}</span>
                    <span v-else><span class="user">{{item.user}}</span>回复<span class="user">{{item.to}}</span>：{{item.content}}</span>
                </div>
            </div>
            <div class="commentState" v-if="showAll" @click="showAll=false">收起<div class="arrUp"></div>
            </div>
            <div class="commentState" v-else @click="showAll=true">查看全部{{info.comments.length}}条评论<div class="arrDown"></div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: '',
    data() {
        return {
            showAll: false,
            isLike:false
        }
    },
    props: {
        like: Number,
        isMine: {
            type: Boolean,
            default: false
        },
        info: Object,
        itemIndex:Number
    },
    methods: {
        toPage(url) {
            console.log(1);
            window.location.href = url;
        },
        thumb(){
            if(this.isLike){
                this.isLike=!this.isLike;
                this.info.likes-=1;
            }else{
                this.isLike=!this.isLike;
                this.info.likes+=1;
            }
        },
        deleteItem(){
            this.$emit("delete",this.itemIndex)
        }
    }
}
</script>

<style lang="scss" scoped>
.cardMain {
    width: 100%;
    box-sizing: border-box;
    border-bottom: 1px solid #eaeaea;
    padding: 16px;
    display: flex;
    justify-content: space-between;

    &>.left {
        width: 40px;
        height: 40px;
        border-radius: 4px;
        background-repeat: no-repeat;
        background-size: contain;
        margin-right: 10px;
    }

    &>.right {
        flex: 1;

        &>.name {
            font-size: 15px;
            font-weight: bold;
            color: #41454d;
            margin-bottom: 2px;
        }

        &>.content {
            font-size: 14px;
            color: #41454d;
            margin-bottom: 2px;
            word-break: break-all;
            margin-bottom: 8px;
        }

        &>.link {
            height: 52px;
            width: 100%;
            box-sizing: border-box;
            padding: 6px;
            background-color: #f7f7f7;
            display: flex;
            align-items: center;
            margin-bottom: 12px;

            &>.img {
                height: 40px;
                width: 40px;
                background-repeat: no-repeat;
                background-size: contain;
                margin-right: 6px;
            }

            &>.content {
                color: #41454d;
                font-size: 13px;
            }
        }

        &>.pic1 {
            margin-bottom: 12px;

            &>img {
                width: 10.75rem;
                height: 10.75rem;
            }
        }

        &>.pic2 {
            margin-bottom: 12px;

            &>img {
                width: 8.375rem;
                height: 8.375rem;
                margin-right: 4px;
            }
        }

        &>.pic3 {
            margin-bottom: 12px;

            &>img {
                width: 5.5rem;
                height: 5.5rem;
                margin-right: 4px;
            }
        }

        &>.stateArea {
            display: flex;
            align-items: center;
            font-size: 12px;
            color: #80858c;
            margin-bottom: 12px;

            &>.likeArea {
                display: flex;
                align-items: center;
                margin-right: 40px;

                &>.dislike {
                    height: 17px;
                    width: 17px;
                    background-image: url("../assets/state-thumbs-up.svg");
                    margin-right: 4px;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
                &>.like {
                    height: 17px;
                    width: 17px;
                    background-image: url("../assets/state-thumbs-up copy.svg");
                    margin-right: 4px;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }

            &>.commentArea {
                display: flex;
                align-items: center;
                margin-right: 40px;

                &>div:first-child {
                    width: 17px;
                    height: 17px;
                    background-image: url("../assets/state-comment.svg");
                    margin-right: 4px;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }

            &>.deleteArea {
                display: flex;
                align-items: center;

                &>div:first-child {
                    width: 17px;
                    height: 17px;
                    background-image: url("../assets/state-delete.svg");
                    margin-right: 4px;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }
        }

        &>.timeArea {
            font-size: 12px;
            color: #acb1b7;
            margin-bottom: 8px;
        }

        &>.commentArea {
            & .commentItem {
                font-size: 14px;
                margin-bottom: 4px;
                color: #41454d;

                & .user {
                    color: #5bafff;
                }

                & .user:first-child {
                    margin-right: 4px;
                }

                & .user:nth-child(2) {
                    margin-left: 4px;
                }
            }

            &>.commentState {
                font-size: 12px;
                color: #acb1b7;
                display: flex;
                align-items: center;
                margin-top: 8px;

                &>.arrUp {
                    width: 12px;
                    height: 8px;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url("../assets/comment-uparrow.svg");
                    margin-left: 4px;
                }

                &>.arrDown {
                    width: 12px;
                    height: 8px;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-image: url("../assets/comment-downarrow.svg");
                    margin-left: 4px;
                }
            }
        }

    }
}
</style>
